.Header {
  width: 100%;
  position: fixed;
  z-index: 21;
  &__innerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px;
    position: relative; }
  &__logo {
    display: inherit;
    a {
      display: inline-block;
      h1 {
        display: inline-block;
        position: relative;
        display: block;
        text-indent: 100%;
        background: url(#{$dir-img}/global/logo-empresariales.png);
        white-space: nowrap;
        overflow: hidden;
        background-repeat: no-repeat;
        background-size: cover;
        width: 340px;
        height: 60px; } } } }


.Header-home {
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,.8);
    transform: translate3d(0, -100%, 0);
    transition: transform .4s ease; } }

.Header-home.show-back {
  &:before {
    transform: translate3d(0, 0, 0); } }

.Nav {
  &__list {}
  &__item {
    display: inline-block;
    margin-left: 40px;
    @extend %upper;
    letter-spacing: 1px;
    position: relative; }
  .boton {
    color: $color-negro;
    transition: background .2s ease;
    &:hover {
      background: $color-primario; } } }
