.Footer {
  padding: 55px 0;
  background: $color-negro;
  color: $color-blanco;
  position: relative;
  &:before {
    content: '';
    background: $color-negro;
    position: absolute;
    top: 0;
    height: 150px;
    width: 100%;
    transform: translate3d(0, -149px, 0);
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%); }
  &__copy {
    border-right: 1px solid $color-blanco;
    img {
      max-width: 190px;
      margin-bottom: 30px; }
    img:nth-of-type(2) {
      margin-bottom: 60px;
      margin-left: 35px; }
    p {
      font-size: 12px; } }
  &__redes {
    h4 {
      color: $color-primario;
      @extend %bold;
      margin-left: 45px;
      font-size: 35px;
      margin-bottom: 60px; }
    ul {
      margin-left: 45px;
      li {
        display: inline-block;
        margin-right: 25px;
        a {
          transition: opacity .3s ease;
          &:hover {
            opacity: .7; }
          img {
            height: 25px; } } } } }
  &__col {
    padding: 40px 15px; } }
