.Pre-footer {
  width: 100%;
  position: relative;
  z-index: 2;
  &__grid {
    background: $color-primario;
    display: grid;
    grid-template-columns: repeat(2, 1fr); } }

.grid-pre-footer {
  color: $color-blanco;
  &__item {
    padding: 45px 60px;
    h5 {
      color: $color-negro;
      @extend %upper;
      @extend %bold;
      letter-spacing: 1px;
      font-size: 30px;
      margin-bottom: 30px;
      min-height: 99px; }
    ul {
      li {
        a {
          color: $color-blanco; } } } }
  &__tel-list {
    li {
      a {
        display: inline-block;
        color: $color-blanco;
        transition: color .3s ease;
        &:hover {
          color: darken($color-blanco, 15%); }
        &:before {
          content: '';
          display: inline-block;
          background: url(../img/global/whatsapp-logo.svg);
          background-repeat: no-repeat;
          -webkit-background-size: cover;
          background-size: cover;
          background-position: center;
          width: 12px;
          height: 12px;
          margin-right: 8px; } } } } }

#know {
  color: #fff;
  display: inline-block;
  margin-top: 15px;
  border-bottom: 1px solid #fff; }
