#menu-mobile {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255,255,255, 1);
  z-index: 20;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
  visibility: hidden;
  transform: translate3d(0, -100%, 0);
  opacity: 0;
  transition: visibility .2s ease, opacity .2s ease, transform .4s ease;
  &:before {
    content: '';
    position: absolute;
    right: 5px;
    bottom: 50px;
    background: url(../img/global/flecha.svg);
    background-repeat: no-repeat;
    width: 300px;
    height: 300px;
    opacity: .1;
    z-index: 4; }
  ul {
    position: relative;
    z-index: 5;
    li {
      @extend %upper;
      a {
        display: block;
        color: $color-primario;
        margin-bottom: 14px;
        font-size: 18px; } }
    li.list-maestria {
      color: $color-negro;
      margin: 35px 0 15px 0;
      @extend %medium;
      letter-spacing: 2px;
      font-size: 13px;
      display: inline-block;
      border-bottom: 1px solid $color-negro;
      padding-bottom: 3px; } } }

#menu-mobile.open {
  transform: translate3d(0, 0, 0);
  visibility: visible;
  opacity: 1; }
