.Contacto {
  width: 100%;
  position: relative;
  padding: 100px 0;
  text-align: center;
  &:before {
    content: '';
    background: #f4f4f4;
    position: absolute;
    top: 0;
    left: 0;
    height: 472px;
    width: 100%;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%); }
  &__title {
    margin-bottom: 75px;
    span {
      color: $color-primario; } }
  &__form {
    background: $color-blanco;
    padding: 30px 40px 60px 40px;
    input, select, textarea {
      border: 0;
      margin-bottom: 25px;
      @extend %upper;
      background: transparent;
      border-radius: 0; }
    select {
      color: #000000;
      -webkit-appearance: none; }
    textarea {
      height: 150px;
      color: #000000; }
    .t-input {
      border-bottom: 1px solid $color-primario;
      padding: 10px 12px 6px 12px;
      font-size: 14px;
      letter-spacing: 1px; }
    input[type="submit"] {
      display: inline-block;
      width: auto;
      background: $color-primario;
      color: $color-blanco;
      padding: 8px 20px;
      border-radius: 7px;
      @extend %medium;
      letter-spacing: 1px;
      font-size: 14px;
      margin: 0;
      margin-top: 20px;
      cursor: pointer;
      transition: background .3s ease;
      &:hover {
        background: darken($color-primario, 20%); } } } }

.Contacto .formu {
  box-shadow: 10px 10px 22px #e2e2e2; }
