/*Uppercase*/
%upper {
  text-transform: uppercase; }

.upper {
  @extend %upper; }

/* Lowe Case */
%lower {
  text-transform: lowercase; }

.lower {
  @extend %lower; }

/* Fuentes */
%regular {
  font-weight: 400; }
.regular {
  @extend %regular; }

%light {
  font-weight: 300; }
.light {
  @extend %light; }

%medium {
  font-weight: 500; }
.medium {
  @extend %medium; }

%bold {
  font-weight: 700; }
.bold {
  @extend %bold; }

/* Botones */
%boton {
  padding: 8px 18px;
  border: 2px solid $color-primario;
  border-radius: 7px;
  @extend %upper;
  letter-spacing: 1px; }
.boton {
  @extend %boton; }

/* Titles */
.g-title {
  @extend %bold;
  font-size: 36px;
  margin-bottom: 35px; }

.g-subtitle {
  @extend %bold;
  @extend %upper;
  font-size: 25px;
  position: relative;
  margin-bottom: 65px;
  span {
    display: block; }
  &:before {
    content: '';
    position: absolute;
    bottom: -15px;
    width: 28px;
    height: 3px;
    left: 50%;
    transform: translateX(-50%);
    background: $color-primario; } }
