* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

$dir-img: '../img';

body, html {
  width: 100%;
  font-size: 16px;
  line-height: 1.5;
  color: $color-gris;
  background: $color-blanco;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400; }

// body
//   overflow: hidden

body.show {
  overflow: auto; }

body.no-scroll {
  overflow: hidden !important; }

h1, h2, h3, h4, h5, h6, figure {
  margin: 0; }

ul, ol {
  margin: 0; }

li {
  list-style: none; }

p {
  margin-bottom: 7px; }

hr {
  background-color: $color-primario;
  border-top: 1px solid $color-primario;
  border-bottom: 1px solid $color-primario;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    left: 50%;
    background: url(../img/global/lines.svg);
    width: 34px;
    height: 24px;
    top: 50%;
    transform: translate3d(-50%, -50%, 0); } }

input, select, option {
  width: 100%; }

textarea {
  width: 100%;
  resize: none; }

input:focus, textarea:focus, select:focus {
  outline: none; }

a {
  &:hover {
    text-decoration: none; } }

::-moz-selection {
  background: $color-blanco;
  color: $color-primario; }

::selection {
  background: $color-blanco;
  color: $color-primario; }

input::-webkit-input-placeholder {
  color: #000000; }

input::-moz-placeholder {
  color: #000000; }

input:-ms-input-placeholder {
  color: #000000; }

input:-moz-placeholder {
  color: #000000; }

#fb-track {
  display: inline-block; }

.wrapper-thanks {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: url(#{$dir-img}/home/principal/back-principal.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  .inner-thanks {
    text-align: center;
    p {
      text-transform: uppercase;
      span {
        display: block; } }
    img {
      max-width: 350px; } } }

.Pop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999;
  background: rgba(0,0,0,.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease, visibility .3s ease;
  &__inner {
    max-width: 600px;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    padding: 15px;
    button {
      position: absolute;
      top: 0;
      right: 0;
      width: 35px;
      height: 35px;
      background: $color-negro;
      border: 2px solid $color-blanco;
      color: $color-blanco;
      border-radius: 50%;
      @extend %upper;
      @extend %medium;
      &:focus {
        outline: none; } } } }


.Pop.open {
  opacity: 1;
  visibility: visible; }

.Pop.close {
  opacity: 0;
  visibility: hidden; }
