@media (max-width: 1199.98px) {
  /* Start Footer */
  .Footer {
    &__copy {
      img:nth-of-type(2) {
        margin-left: 0; } } }
  /* End Footer */ }

@media (max-width: 991.98px) {
  .Principal__title {
    margin-bottom: 65px; }

  /* Start Principal Home*/
  .Nav-bottom {
    &__inner {
      height: 85px; }
    &__item {
      font-size: 16px; } }
  /* End Principal Home*/

  /* Start Pre Footer */
  .grid-pre-footer {
    &__item {
      padding: 45px 30px; } }
  /* End Pre Footer */

  /* Start apoyo */
  .Apoyo {
    &__icons {
      .col-lg-3 {
        margin-bottom: 20px; } } }
  /* End apoyo */
  .Convenio {
    .mb-order-1 {
      order: 1; }
    .mb-order-2 {
      order: 2; }
    .wrapper-anchors {
      padding-left: 15px; } } }

@media (max-width: 767.98px) {
  /* Start Header */
  .Header {
    &__innerContainer {
      padding: 10px 15px; }
    &__logo {
      a {
        h1 {
          width: 135px;
          height: 47px; } } } }

  .Header-home {
    &:before {
      transform: translate3d(0, 0, 0); } }
  /* End Header */

  /* Start Principal Home*/
  .Principal {
    &__title {
      span:nth-of-type(1) {
        font-size: 25px;
        margin-bottom: 15px; }
      span:nth-of-type(2) {
        font-size: 23px; } } }

  .Nav-bottom {
    display: none; }
  /* End Principal Home*/

  /* Start Pre Footer */
  .Pre-footer {
    &__grid {
      grid-template-columns: 1fr; } }

  .grid-pre-footer {
    &__item {
      padding: 30px 20px;
      h5 {
        font-size: 23px;
        margin-bottom: 20px;
        min-height: auto; } } }
  /* End Pre Footer */

  /* Start Footer */
  .Footer {
    &:before {
      top: -70px;
      height: 220px; }
    &__copy {
      border-right: 0;
      img {
        max-width: 160px;
        margin-bottom: 20px;
        display: block; }
      p {
        color: #757575;
        margin-bottom: 0; } }
    &__redes {
      h4 {
        margin-left: 0px;
        margin-bottom: 30px; }
      ul {
        margin-left: 0px;
        li {
          a {
            img {
              height: 18px; } } } } }
    &__col {
      padding: 25px 15px; } }
  /* End Footer */ }

@media (max-width: 575.98px) {
  .Principal {
    padding: 190px 0 130px 0; }

  /* Start Footer */
  .Footer {
    &:before {
      top: 0px;
      height: 265px; } }
  /* End Footer */

  /* Start Form Contacto */
  .Contacto {
    padding: 60px 0 100px 0;
    &__form {
      padding: 30px 20px 60px 20px; } }
  /* End Form Contacto */ }
