.Convenio {
  padding: 80px 0 40px 0;
  text-align: center;
  .row {
    margin-bottom: 60px; }
  p {
    margin-bottom: 10px; }
  h5 {
    margin-bottom: 10px; }
  img {
    margin-bottom: 30px; }
  ul.lic-list {
    li.lic-item {
      margin-bottom: 15px;
      .wrapper-title {
        position: relative;
        &:before {
          content: '';
          width: 5px;
          height: 5px;
          background: $color-primario;
          border-radius: 50%;
          display: inline-block;
          transform: translate3d(0, -2px, 0); }
        .icon {
          width: 20px;
          height: 20px;
          margin: 0; }
        a {
          color: $color-negro;
          transition: color .3s;
          &:hover {
            color: $color-primario; } } }
      ul {
        padding-left: 10px;
        li {
          font-size: 14px;
          margin: 0; } } } }
  .wrapper-anchors {
    padding-left: 20px; } }


.owl-dots {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translate3d(-50%, 0, 0);
  button {
    width: 15px;
    height: 15px;
    background: $color-primario !important;
    margin: 0 8px;
    border: 1px solid $color-primario !important;
    border-radius: 50%;
    &.active {
      background: #ffffff !important; }
    &:focus {
      outline: 0 !important; } } }

.owl-prev, .owl-next {
  margin: 0;
  /* margin: 0 18px !important */
  span {
    font-size: 25px;
    padding: 0px 16px;
    line-height: 25px;
    color: #ffffff;
    &:focus {
      outline: 0 !important; } } }
