.Principal {
  width: 100%;
  padding: 150px 0 80px 0;
  background: url(#{$dir-img}/home/principal/back-principal.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
  transition: height 9999999999s ease;

  &__container {
    display: flex;
    align-items: center;
    transition: height 9999999999s ease; }
  &__title {
    color: $color-negro;
    span {
      display: block;
      position: relative; }
    span:nth-of-type(1) {
      @extend %bold;
      @extend %upper;
      font-size: 38px; }
    span:nth-of-type(2) {
      @extend %light;
      font-size: 28px;
      margin-bottom: 6px;
      &:after {
        content: '';
        position: absolute;
        bottom: -25px;
        width: 27px;
        height: 3px;
        background: $color-primario;
        left: 3px; } } }
  &__wrapper-form {
    text-align: center; } }

.anim-toright {
  transition: transform .3s ease;
  &:hover {
    transform: translate3d(8px, 0, 0); } }
